
  import { defineComponent,computed,ref ,onMounted, onBeforeUnmount } from "vue";

  import axios from 'axios';

  
  
  export default defineComponent({
    name: "FindRamenApi",
    components: {
    },
    setup(){
        //google地圖zoom=14的座標 中山區=行天宮站 南港區=南港捷運站 萬華=龍山寺捷運站
        let Taipei_Districts = [
       
         { name:"萬華區",latitude:"25.0278978" , longitude:"121.5071962" },
         { name:"中正區",latitude:"25.0292957" , longitude:"121.4816144" },
         { name:"大同區",latitude:"25.0644977" , longitude:"121.474346" },
         { name:"大安區",latitude:"25.026306"  , longitude:"121.5232463" },
         { name:"中山區",latitude:"25.0638366" , longitude:"121.5093806" },
         { name:"士林區",latitude:"25.0987204" , longitude:"121.5098718" },
         { name:"北投區",latitude:"25.1186256" , longitude:"121.4976834" },
         { name:"松山區",latitude:"25.0601713" , longitude:"121.5387077" },
         { name:"信義區",latitude:"25.0287142" , longitude:"121.5723161" },
         { name:"內湖區",latitude:"25.0725201" , longitude:"121.5547487" },
         { name:"南港區",latitude:"25.0530877" , longitude:"121.5978411" },
         { name:"文山區",latitude:"24.9884536" , longitude:"121.5287444" },
        ]  
        //僅放入比較熱門的區域
        let Xinpei_Districts = [
         { name:"蘆洲區",latitude:"25.0879187" , longitude:"121.4339136" },
         { name:"三重區",latitude:"25.0666174" , longitude:"121.447105" },
         { name:"泰山區",latitude:"25.0527323" , longitude:"121.3921064" },
         { name:"新莊區",latitude:"25.0290977" , longitude:"121.4027341" },
         { name:"樹林區",latitude:"24.9815654" , longitude:"121.3878606" },
         { name:"板橋區",latitude:"25.0088113" , longitude:"121.4371" },
         { name:"中和區",latitude:"24.9902867" , longitude:"121.4774026" },
         { name:"永和區",latitude:"25.0075937" , longitude:"121.493457" },
         { name:"新店區",latitude:"24.9657177" , longitude:"121.5159416" },
         { name:"汐止區",latitude:"25.0677944" , longitude:"121.6233853" },
         { name:"淡水區",latitude:"25.1856258" , longitude:"121.4265596" },
         { name:"林口區",latitude:"25.0795722" , longitude:"121.3721345" },
        ]

        let RamenTags = [ "豚骨 拉麵","雞白湯 拉麵", "魚介 拉麵", "二郎 拉麵","煮干 拉麵","家系 拉麵"  ]
        let SendApiData = { SearchKeyword:"拉麵" , 
         latitude:"25.0638366" , longitude:"121.5093806",
        }
        let RamenRestaurantList =ref([
            { Id:"1",Name:"test test test  aaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaa test",Address:"11",Phone:"6", Rating:"1",RatingCount:"1328",WebSite:"www.ca",AddedFavorite: false },

        ])
        let SelectedArea = ref("中山區")
        let isDetailContent = ref(false)
        let isOrdered =  ref(false)


   const ApiKey = 'AIzaSyCrMXoReTTI0NHyKjsv_sgF6enzQGHzDrM';
   const ApiUrl = `https://places.googleapis.com/v1/places:searchText`;

      

  


    const  FindingRamen = async  function(){

        console.log("開始API")
        await axios.post( ApiUrl,
        { 
          textQuery: SendApiData.SearchKeyword,
        //次要
          maxResultCount:"5",
          languageCode: "zh-TW",
          locationBias: {
            "circle": {
              "center": {
                "latitude": SendApiData.latitude ,
                "longitude": SendApiData.longitude
              },
              "radius": 2000.0
            }
          },
        },
        { headers:{  "Content-Type": "application/json",
                     "X-Goog-FieldMask":"places.id,places.displayName,places.formattedAddress,places.nationalPhoneNumber,places.rating,places.userRatingCount,places.websiteUri",
                     "X-Goog-Api-Key": ApiKey
          }
        }
        )
        .then((Response) =>{
            console.log(Response)
            GetRamenInfo(Response)
        })
        .catch(  (err) => {
          console.log(err)
        })
       
     }
     const SendToApi_Area = (lat:string , lon:string , Area:string)=>{
        SelectedArea.value = Area
        SendApiData.latitude = lat
        SendApiData.longitude = lon
        console.log("傳送經緯度")
     }
     const SendToApi_keyword = (keyword:string  )=>{
        SendApiData.SearchKeyword = keyword
        
        
        console.log(`傳送_keyword ${keyword}`)
     }
     
     const GetRamenInfo = (res:any)=>{
        console.log("處理json")
        isOrdered.value = true
         RamenRestaurantList.value = res.data.places.map( (list:any )=>{
          let  { text : Name } = list.displayName
          let  { formattedAddress : Address,
                 id : Id,
                 nationalPhoneNumber : Phone,
                 rating : Rating,
                 userRatingCount : RatingCount,
                  websiteUri : WebSite ,
                  
               } = list
          if(!Phone){
            Phone  = "No contact"
          }
          if( parseInt( RatingCount,10 ) > 999 ){
            RatingCount =  "999+"
          }
            
               return { Id:Id , Name:Name, Address:Address,
                        Phone:Phone ,Rating:Rating,RatingCount:RatingCount ,WebSite:WebSite , AddedFavorite : false}
        })
     }

     let FadeDirection = ref("")
     const NextBtn  = ()=>{
        FadeDirection.value = "Right"
       ItemNo.value++
       if(ItemNo.value > RamenRestaurantList.value.length - 1){
           ItemNo.value = 0
       }
     }
     const PreviousBtn  = ()=>{
        FadeDirection.value = "Left"
       ItemNo.value--
       if(ItemNo.value < 0){
           ItemNo.value = RamenRestaurantList.value.length - 1
       }
     }

     const WrapDetail =  ref<HTMLDivElement | null>(null)
     let isPlayDetailAnimation:boolean
     const CloseDetailInfo  =  ()=>{
      isDetailContent.value= false
        WrapDetail.value?.classList.add('DropAnimation')
     }
     const ReflashDropAnima  = ()=>{
        WrapDetail.value?.classList.remove('DropAnimation')
        isPlayDetailAnimation = false
     }


     let ItemNo = ref(0)
     const ShowDetail  =  (index:number)=>{
    //注意要根據DropAnimation的時間調整，設定在動畫遮住內容時執行(改內容)
         setTimeout(() => {
          ItemNo.value = index
         }, 500)
        if(isPlayDetailAnimation === true)
         return


        FadeDirection.value =""
        WrapDetail.value?.classList.add('DropAnimation')
        isDetailContent.value= true
        isPlayDetailAnimation = true
       
     }
//抽獎功能相關

     let FavoriteList = ref<string[]>([])
     //陣列預設填滿false
     
     const AddFavorite  = (ind:any )=>{
      let HandleItem = RamenRestaurantList.value[ind].Name

  RamenRestaurantList.value[ind].AddedFavorite = !RamenRestaurantList.value[ind].AddedFavorite

      if(  RamenRestaurantList.value[ind].AddedFavorite === true){
        FavoriteList.value.push( HandleItem )
      }
      else{
        let Del_index = FavoriteList.value.indexOf( HandleItem);
        if (Del_index !== -1) {
         FavoriteList.value.splice(Del_index, 1);
        }
      }
     }

     const LoadLocalStorage  = ()=>{
  
      const LocalFavoriteList = localStorage.getItem('FavoriteList')
      console.log('LocalFavoriteList:', LocalFavoriteList);
      if(LocalFavoriteList){
        FavoriteList.value = JSON.parse(LocalFavoriteList)

      }
    }

     const DeleteFavoriteArr  = (Favorite_index:number)=>{

        let DeleteItem_Name =  FavoriteList.value.splice(Favorite_index, 1).join(''); //用join將陣列轉成普通字串
        let DeleteItem_Ind  =   RamenRestaurantList.value.findIndex( (x)=>  x.Name == DeleteItem_Name )
        if (DeleteItem_Ind !== -1) {
          console.log(`剩下最愛: ${FavoriteList.value} `)
          RamenRestaurantList.value[DeleteItem_Ind].AddedFavorite = false;
        }
        SaveDrawinglotsData()
     }

     const FavoriteNum = computed(() => {
       return FavoriteList.value.length
    });

    let isDrawlots =ref(false)
    let DrawinglotsIndex =ref()

    const Drawinglots  = ()=>{
      if(FavoriteList.value.length <= 1 )
        return

      DrawinglotsIndex.value = Math.floor( Math.random() * FavoriteList.value.length );
      console.log(DrawinglotsIndex.value)
      isDrawinglotsStarted.value = true
   //   DrawingListPointer.value?.classList.add("DrawingListPointer")
    }

     const DrawingListPointer = ref<HTMLDivElement | null>(null)
     const DrawingList = ref<HTMLDivElement | null>(null)
      let BoxPercentWidth = 100
      let RunningDrawingTimes =0
      let isDrawinglotsStarted =ref(false)
    const PlayDrawingAnima  = ()=>{

     let     ItemPercentW   =  BoxPercentWidth / FavoriteList.value.length 
     let     MoveDistance = ItemPercentW * (DrawinglotsIndex.value +1) - Math.floor(ItemPercentW / 2)
       RunningDrawingTimes++

      if(RunningDrawingTimes === 2){
        
        console.log(`MoveDistance: ${MoveDistance} -% ItemW::${ItemPercentW}`)
        DrawingListPointer.value?.style.setProperty("--MoveDistance" , `${MoveDistance}%` )
        
      }
    }
    let isHighLight =ref(false)

     const ResetDrawinglots  = ()=>{
      isDrawinglotsStarted.value = false
      DrawingListPointer.value?.style.setProperty("--MoveDistance" , `${BoxPercentWidth}%` )
      RunningDrawingTimes = 0
      isHighLight.value = false
     }
     const CloseDrawinglots  = ()=>{
      ResetDrawinglots()
      SaveDrawinglotsData()
      isDrawlots.value = false
     }


    const DrawinglotsResult = computed(() => {

       return FavoriteList.value[DrawinglotsIndex.value]
     
    });
    const CheckHightLight = (index:number) => {
      if(index === DrawinglotsIndex.value && isHighLight.value === true)
       return  true
    };
    const SaveDrawinglotsData = () => {
      localStorage.setItem('FavoriteList', JSON.stringify(FavoriteList.value));
    };

   onMounted(() => {
    LoadLocalStorage();
   
 
  });
    onBeforeUnmount(() => {
      SaveDrawinglotsData()
     
    });

        return{ FindingRamen ,SendToApi_Area , SendToApi_keyword,
             Taipei_Districts , Xinpei_Districts , RamenTags , NextBtn,PreviousBtn,ItemNo,
             SendApiData , SelectedArea , RamenRestaurantList , ShowDetail,
             FadeDirection,isDetailContent,
              CloseDetailInfo , WrapDetail ,ReflashDropAnima ,  isOrdered
             ,AddFavorite,FavoriteNum,FavoriteList,DrawingList
            ,isDrawlots,DrawinglotsResult,Drawinglots,CloseDrawinglots
            ,PlayDrawingAnima,DrawingListPointer,ResetDrawinglots
            ,isDrawinglotsStarted,isHighLight,CheckHightLight,DeleteFavoriteArr
          }
    }
  });

